import React, { } from "react";
import { FiEdit, FiXCircle } from "react-icons/fi";


const ItemCategoria = (props) => {
console.log(props)
    return (
        <tr>
            <td>{props.categoria.id}</td>
            <td>{props.categoria.categoria}</td>
            <td>
                <div><FiEdit onClick={() => {props.categoriaEdit(props.categoria); props.visibleModalEdit(true) }} /></div>
                <div><FiXCircle onClick={() => {props.visibleModalDelete(true) }} /></div>
            </td>
        </tr>
    )
}

export default ItemCategoria;