import Axios from "axios"



export const getCategorias = (url = 'http://eventapi.ramersonrodrigues.com/api/categoria') => (
    Axios.get(url)
        .then(res => {
            console.log(res.data);
            return res
        }).catch(error => {
            console.log(error)
            return false
        })
)


export const addCategorias = (categoria) => (
    Axios.post("http://eventapi.ramersonrodrigues.com/api/categoria", {
        "categoria": categoria.categoria,
    })
        .then(res => {
            console.log(res.data)
            if (typeof res.data.id === "undefined") {
                return false
            } else {
                return res.data
            }
        }).catch(error => {
            console.log(error) 
            return false
        })
)

export const editCategoria = (categoria) => (
    Axios.post("http://eventapi.ramersonrodrigues.com/api/categoria/" + categoria.id, categoria)
        .then(res => {
            console.log(res.data)
            if (typeof res.data.id === "undefined") {
                return false
            } else {
                return res.data
            }
        }).catch(error => {
            console.log(error)
            return false
        })
)

