import { call, put } from "redux-saga/effects";
import { getCategorias, addCategorias, editCategoria } from "../../api/categoriaService";


export function* sagaCategorias(action) {
    console.log("teste")
    const categorias = yield call(getCategorias, action.payload);
    console.log(categorias);
    yield put({
        type: "SET_CATEGORIAS",
        payload: categorias
    })
}

export function* sagaAddCategorias(action) {
    console.log(action.payload)
    const categoria = yield call(addCategorias, action.payload);
    const categorias = yield call(getCategorias);
    yield put({
        type: "SET_CATEGORIAS",
        payload: categorias
    })
}

export function* sagaEditCategoria(action) {
    console.log("teste")
    const categoria = yield call(editCategoria, action.payload);
    const categorias = yield call(getCategorias);
    yield put({
        type: "SET_CATEGORIAS",
        payload: categorias
    })
}



