import { call, put } from "redux-saga/effects";
import { getPerfis } from "../../api/perfilService";


export function* sagaPerfis(action) {
    console.log("teste")
    const perfis = yield call(getPerfis, action.payload);
    console.log(perfis);
    yield put({
        type: "SET_PERFIS",
        payload: perfis
    })
}
