import React, { useEffect, useState } from "react";
import { Container, Pages, } from "./style";

import {  FiPlus, } from "react-icons/fi";
import { IoMdArrowDropleft, IoMdArrowDropright } from "react-icons/io";
import { useDispatch, useSelector } from "react-redux";

import ItemCategoria from "../itemCategoria";
import ModalAddUser from "../modalAddCategoria";
import ModalEditUser from "../modalEditCategoria";
import ModalDeleteUser from "../modalDeleteCategoria";

const Grade = () => {

    let categorias = []
    const dispatch = useDispatch();
    categorias = useSelector(state => state.categoriaReducer);

    const [visibleModalDelete, setVisibleModalDelete] = useState(false)
    const [visibleModalAdd, setVisibleModalAdd] = useState(false)
    const [visibleModalEdit, setVisibleModalEdit] = useState(false)
    const [categoriaEdit, setCategoriaEdit] = useState(false)

    useEffect(() => {
        dispatch({
            type: "SAGA_SET_CATEGORIAS"
        })
    }, [])

    function dados() {
        if (categorias === false) {
            return <span>Erro</span>
        } else if (categorias.length === 0) {
            return <span>Carregando</span>
        } else {
            return (
                categorias.data.data.map((item) => (
                    <ItemCategoria
                        visibleModalEdit={setVisibleModalEdit}
                        visibleModalDelete={setVisibleModalDelete}
                        categoriaEdit= {setCategoriaEdit}
                        categoria={item}
                        key={"" + item.id} />
                ))
            )
        }
    }

    function pages() {
        if (categorias === false) { 
            return <span>Erro</span>
        } else if (categorias.length === 0) {
            return <span>Carregando</span>
        } else {
            console.log(categorias.data)
            return (
                <Pages>
                    <div>
                        <IoMdArrowDropleft size={30} onClick={() => {
                            categorias.data.prev_page_url != null && dispatch({ type: "SAGA_SET_CATEGORIAS", payload: categorias.data.prev_page_url })
                        }} />
                    </div>
                    <span>
                        {
                            categorias.data.to + ' de ' + categorias.data.total
                        }
                    </span>
                    <div>
                        <IoMdArrowDropright size={30} onClick={() => {
                            categorias.data.next_page_url != null && dispatch({ type: "SAGA_SET_CATEGORIAS", payload: categorias.data.next_page_url })
                        }} />
                    </div>
                </Pages>
            )
        }
    }

    return (
        <Container>
            {visibleModalDelete && <ModalDeleteUser visible={setVisibleModalDelete} />}
            {visibleModalAdd && <ModalAddUser visible={setVisibleModalAdd} />}
            {visibleModalEdit && <ModalEditUser visible={setVisibleModalEdit} categoria={categoriaEdit} />}
            <table >
                <thead>
                    <tr>
                        <th>ID</th>
                        <th>Categoria</th>
                        <th>
                            <div><FiPlus onClick={() => { setVisibleModalAdd(true) }} /></div>
                        </th>
                    </tr>
                </thead>
                <tbody>
                    {dados()}
                </tbody>
            </table>
            {pages()}
        </Container >

    )
}

export default Grade;