import React, { useState } from "react";
import Modal from "react-modal";
import { useDispatch } from "react-redux";

import { Container } from "./style";
import { colors } from "../../../../assets/colors";

const ModalEditCategoria = (props) => {

    console.log(props)

    const [categoria, setCategoria] = useState(props.categoria.categoria)

    const dispatch = useDispatch()

    function editCategoria() {
        if (categoria === '') {
            alert("Preencha...")
        } else {
            dispatch({
                type: "SAGA_EDIT_CATEGORIA",
                payload: {
                    categoria: categoria,
                    id: props.categoria.id
                }
            })
            props.visible(false)
        }
    }

    return (
        <Modal isOpen={true} style={{
            content:{
                backgroundColor: colors.primaryColor,
                display:"flex",
                justifyContent: "center",
                alignItems: "center",
            }
        }}>
            <Container>
                <input
                    placeholder={"Categoria:"}
                    value={categoria}
                    onChange={(e) => setCategoria(e.target.value)}
                />
                <button onClick={() => { editCategoria() }} >SALVAR</button>
                <button onClick={() => { props.visible(false) }}>SAIR</button>
            </Container>
        </Modal>
    )
}

export default ModalEditCategoria;