import React, { useEffect, useState } from "react";
import { Container, Pages, } from "./style";

import {  FiPlus, } from "react-icons/fi";
import { IoMdArrowDropleft, IoMdArrowDropright } from "react-icons/io";
import { useDispatch, useSelector } from "react-redux";

import ItemPerfil from "../itemPerfil";

const Grade = () => {

    let perfis = []
    const dispatch = useDispatch();
    perfis = useSelector(state => state.perfilReducer);

      useEffect(() => {
        console.log("teste inicio");
        dispatch({
            type: "SAGA_SET_PERFIS"
        })
    }, [])

    function dados() {
        if (perfis === false) {
            return <span>Erro</span>
        } else if (perfis.length === 0) {
            return <span>Carregando</span>
        } else {
            return (
                perfis.data.map((item) => (
                    <ItemPerfil perfil={item} />
                ))
            )
        }
    }

    function pages() {
        if (perfis === false) { 
            return <span>Erro</span>
        } else if (perfis.length === 0) {
            return <span>Carregando</span>
        } else {
            console.log(perfis.data.data)
            return (
                <Pages>
                    <div>
                        <IoMdArrowDropleft size={30} onClick={() => {
                            //users.data.prev_page_url != null && dispatch({ type: "SAGA_SET_USERS", payload: users.data.prev_page_url })
                        }} />
                    </div>
                    <span>
                        {
                            //users.data.to + ' de ' + users.data.total
                        }
                    </span>
                    <div>
                        <IoMdArrowDropright size={30} onClick={() => {
                            //users.data.next_page_url != null && dispatch({ type: "SAGA_SET_USERS", payload: users.data.next_page_url })
                        }} />
                    </div>
                </Pages>
            )
        }
    }

    return (
        <Container>
            <table >
                <thead>
                    <tr>
                        <th>ID</th>
                        <th>Perfil</th>
                        
                    </tr>
                </thead>
                <tbody>
                    {dados()}
                </tbody>
            </table>
            {pages()}
        </Container >

    )
}

export default Grade;